        .row.p-3.colort4.marq
          p Las herramientas de gestin facilitan el anlisis de la organizacin y aumentan el control, ampliando el conocimiento de los procesos y actividades que se desarrollan, que en un ejercicio compartido con los diferentes actores de reas funcionales alinean la organizacin en la resolucin de problemas y la definicin de soluciones, fijacin de objetivos, entre otros. En estas herramientas se puede hacer revisiones de variables cualitativas o cuantitativas para toma de decisiones frente a alternativas empresariales. 
