    p.mt-5 Por lo anterior, se puede decir que las herramientas de <strong>anlisis empresarial</strong>, son todos los mtodos que se consideren para hacer ms eficaz la administracin de una empresa al facilitar la toma de decisiones. Entre las ms utilizadas estn las siguientes:
    ol.lista-ol--cuadro.mt-4
