        h4 Diagrama de causa  efecto
    .row.mt-4
      .col-lg-5
        p Es una herramienta para identificar de manera estructurada las causas de un problema o efecto evidenciado a partir de un esquema grfico que se asemeja a una espina de pescado que es como tambin se denomina a esta herramienta.
        p.mt-3 La tcnica de utilizacin refiere a identificar las causas principales de un problema o efecto negativo en la empresa, posteriormente identificar las subcausas o variables de causas relacionadas con las principales que estn contribuyendo con el problema, y que son de carcter secundario e inclusive pueden ser de carcter terciario como en el caso de proveedores, que infieren en la disponibilidad de materiales de materias primas, insumos o suministros, y dentro de este problema, que estos proveedores estn concentrados, o que los materiales de los proveedores solo se consiguen en ciertas cantidades no disponibles para la empresa. Es muy verstil para analizar y resolver problemas complicados, pues permite un anlisis desde los factores operativos ms relevantes de la empresa.
