        h4 Diagrama de Pareto
    p.mt-4 Esta herramienta es una forma particular de un diagrama de barras verticales utilizado para identificar los pocos vitales y los muchos triviales que puede tener una empresa, donde los primeros son en gran medida los responsables de la mayor parte de los efectos negativos de los problemas en la empresa, donde caso siempre se asocian a una relacin 70-30 o 80-20, esto es, que del total de los mltiples problemas que puede tener una empresa entre el 70% al 80% se concentran en pocas causas, y el resto de problemas que son mltiples representan entre el 20% al 30% de los casos, por lo cual la empresa debe concentrar su atencin en resolver esos pocos problemas que son vitales para el negocio pues tienen mucho efecto, y que solucionando estos probablemente se resuelven muchos de los aspectos menores .
