            h4 Matriz BCG
        p La matriz BCG (Boston Consulting Group), es una herramienta desarrollada por esta compaa como instrumento de anlisis grfico de anlisis del portafolio de productos, a partir de la posicin que ocupa dentro de una empresa cada producto y su tendencia; con el fin de tomar decisiones para cada uno de estos, en bsqueda de la maximizacin de utilidades y beneficios.
        p Es una matriz de cuatro cuadrantes, cada uno distinguido por una figura de icono, y donde cada uno de los cuales obedece a las caractersticas de comportamiento de las lneas de producto o los productos en s.
        p En el caso de los productos agropecuarios, se puede realizar una por cada tipo de producto y mercado, siendo que los productos pecuarios, se podrn comparar entre s, mientras que los productos agrcolas se podrn comparar aparte, con el fin de guardar una equilibrada comparacin, ya que los lotes de inversin son diferentes.

      .col-lg-5
        .titulo-sexto.color-acento-botones
          h5 Figura: Matriz BCG

          span Crecimiento del producto
        .row.justify-content-center
          .col-lg-10(data-aos="fade-left")
            img(src='@/assets/curso/tema3/img4.svg', alt='Texto que describa la imagen')
    .row.p-3.colort4.mt-4
      p Estos cuadrantes se definen entre dos variables, el crecimiento en el mercado es decir como aumentan las ventas, y el eje de participacin de mercado, que es cunto de lo que se vende en el sector o industria contribuye la empresa, o cunto de lo que vende la empresa representa cada producto. Comprendido esto la labor diagnstica de la empresa es ubicar su portafolio de productos en los cuadrantes segn corresponda, puede haber varios en un solo cuadrante o cuadrantes sin productos.
    p.mt-4 Hay que tener en cuenta que esta clasificacin de productos por categoras se da a partir de:
    .row.mt-5
      .col-lg-12(data-aos="fade-up")
        .tarjeta-avatar-b.mb-5
          .tarjeta-avatar-b__img
            img.waq(src='@/assets/curso/tema3/ico1.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul.color-primario.sombra
            .p-4
              p.mt-4.line <strong>Productos estrella.</strong>
              p Son aquellos con gran crecimiento y que estn logrando buena participacin de mercado. A estos productos ubicados en este cuadrante hay que estimularlos seguirles invirtiendo y no retirarles utilidades, hasta que no se vuelvan vacas lecheras.
      .col-lg-12(data-aos="fade-up")
        .tarjeta-avatar-b.mb-5
          .tarjeta-avatar-b__img
            img.waq(src='@/assets/curso/tema3/ico2.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul.color-primario.sombra
            .p-4
              p.mt-4.line <strong>Productos interrogantes.</strong>
              p Son aquellos productos que tienen un rpido crecimiento, pero an poca cuota de participacin en el mercado, regularmente porque son nuevos. Se recomienda seguir invirtindoles porque son semillas que crecern y se convertirn en productos estrella o vacas lecheras, se tiene que supervisar mucho porque tambin fcilmente se pueden convertir en productos perros, porque las personas consumen al inicio por exploracin, pero si no gustan, no recompran, no crecern.
      .col-lg-12(data-aos="fade-up")
        .tarjeta-avatar-b.mb-5
          .tarjeta-avatar-b__img
            img.waq(src='@/assets/curso/tema3/ico3.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul.color-primario.sombra
            .p-4
              p.mt-4.line <strong>Productos vaca lechera:</strong>
              p Se denominan as porque son los productos ms desarrollados que producen los ingresos ms altos en la empresa, ya estn bien posicionados en el mercado, aunque cada vez es ms difcil aumentar su crecimiento de ventas, porque el mercado ya est saturado o es inflexible. De los recursos de estos productos se pueden retirar utilidades con que vive la empresa e invertir en los otros productos como estrellas e interrogantes incluso.
      .col-lg-12(data-aos="fade-up")
        .tarjeta-avatar-b.mb-5
          .tarjeta-avatar-b__img
            img.waq(src='@/assets/curso/tema3/ico4.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul.color-primario.sombra
            .p-4
              p.mt-4.line <strong>Productos perro.</strong>
              p Estos productos no presentan crecimiento en ventas y baja participacin de mercado frente a lo que se vende en el sector o industria, por ende, no contribuyen en los beneficios de la empresa regularmente, incluso llega a ser negativa la utilidad. En estos casos se recomienda retirar del portafolio y oferta de productos estas referencias. En ocasiones hay que mantenerlos ya que representan la tradicin de la empresa, tienen el posicionamiento y generan la confianza en los otros productos de la empresa, aun cuando su contribucin a la empresa no es alto, regularmente fueron vacas lechera, que por cambios en las tendencias de consumo o por que salieron productos para la misma funcin o servicio con mejor tecnologa o eficiencia se quedaron relegados, se podra desarrollar esos productos, para ver si se recompone su crecimiento, decisiones que deben tomar los empresarios al diagnosticar las potencialidades del producto.
