      p.mt-5 A partir de la ubicacin de estos productos del portafolio de la empresa, se deben establecer las medidas estratgicas para el sostenimiento, ampliacin y defensa de estos productos, de continuar siendo atractivos y rentables para la empresa. para el caso del ejemplo es evidente que los 3 productos deben continuar y que en especial los productos 2 y 3, deben ser tratados con acciones que les permita continuar su desarrollo y crecimiento, mientras que en el producto nmero uno las iniciativas buscarn la defensa de la participacin en el mercado y la sostenibilidad del nivel de ventas en el largo plazo.
