              h4 Ciclo de vida del producto
      p.mt-4 Es la representacin grfica de las diferentes fases por las que atraviesan los productos desde su desarrollo hasta su retiro de la oferta del portafolio de productos de la empresa, est constituido por cuatro fases plenamente identificables
      .row.mt-4
        .col-lg-1
        .col-lg-9(data-aos="fade-up")
          .titulo-sexto.color-acento-botones
            h5 figura 4
            span Ciclo de vida del producto
          img(src='@/assets/curso/tema3/img9.svg' alt='AvatarTop')
        .col-lg-2
      ul.lista-ul.mb-5.mt-4
        li 
          i.fas.fa-angle-right.colorvi
          | De esta manera se presentan cuatro fases: fase de introduccin, fase de crecimiento, fase de madurez, fase de declive, las cuales se describen a continuacin 
      
      AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
        .row.justify-content-center(titulo="Fase de introduccin")
          .col-md-11.mb-4.mb-md-0
            p Todo producto empieza comercialmente en el momento en el que se lanza al mercado, se trata de una novedad para los clientes o consumidores porque lo desconocen, no conocen su desempeo, caractersticas etc.; lo que implica que las ventas sean bajas al principio, especialmente si la empresa o marca asociada no es muy conocida, por ello requiere de inversiones iniciales para su lanzamiento y distribucin, colocacin en tiendas, etc., haciendo por llegar a los clientes a travs de estrategias de <i>marketing</i>, como promociones y publicidad.
        .row.justify-content-center(titulo="Fase de crecimiento")
          .col-md-11.mb-4.mb-md-0
            p Despus de la introduccin de un producto, se da la fase del crecimiento, como un aumento de ventas en consecuencia de las acciones que se adelantaron durante la fase de introduccin. Los consumidores conocieron el producto gracias a la promocin y publicidad y dems estrategias de <i>marketing</i> que se implementaron, por lo cual han decidido confiar en la marca y seguir comprando o recomendar su compra. 
            p Esta fase de crecimiento se caracteriza por el aumento sostenido de las ventas y porque se empiezan a consolidar beneficios o utilidades del producto, hay que tener en cuenta que aparecern competidores que intentarn hacer lo mismo y mejorar el producto; razn por la cual en esta fase es importante que la empresa siga llevando su estrategia de <i>marketing</i> para diferenciarse de la competencia y hacer mejoras al producto como ventaja competitiva. El cliente reconoce y le es muy importante que le mantengan tanto la calidad del producto como el precio, por eso lo importante de aadir un valor o servicio extra. Por otro lado, como el producto se vuelve tendencia, es muy importante el aumento de la distribucin o presencia comercial donde acta para aumentar nuevos clientes.
            p En esta fase la empresa recupera su inversin y empieza a obtener beneficio o utilidad, por lo cual se debe tener una adecuada inversin para que esta curva de crecimiento se aumente.
        .row.justify-content-center(titulo="Fase de madurez")
          .col-md-11.mb-4.mb-md-0
            p Esta fase es en la que el producto obtiene grandes niveles de ventas en unidades, y por ende en dinero, por lo cual tambin tendr que afrontar gran competencia y estos niveles de ventas pueden caer rpidamente. Muchas empresas se suelen estabilizar en esa zona porque han comprendido las exigencias de los clientes y estos atienden su oferta adecuadamente, pero la empresa empieza a tener dificultades para crecer en ventas, por la competencia que es mayor, y porque el mercado al igual se satura, se cubre y no es fcil ahora vender ms, no hay ms clientes a quien vender, por eso la empresa debe buscar nuevos mercados o segmentos geogrficos para llevar su producto, donde no ha tenido actividad  comercial y pueda avanzar en nuevas ventas.
            p En esta fase la empresa obtiene utilidades sobre las cuales puede disponer, y solventar gastos propios de la empresa y su actividad, as como disponer de recursos para promover iniciativas de nuevos productos, etc.
        .row.justify-content-center(titulo="Fase de declive")
          .col-md-11.mb-4.mb-md-0
            p Esta fase se caracteriza por un cambio en las preferencias de compra de los clientes, una menor demanda del producto por cambios en las tendencias o modas, etc., lo que se expresa en la fuerte cada de ventas; de ser este el caso el declive lo sufre tambin la competencia por lo que hay que entender que todos los productos tienen una vida limitada (salvo algunas excepciones que se amplan en el tiempo pero con grandes esfuerzos de mejora), por lo que llega un momento en el que entran a no aportar a la empresa beneficios ni utilidades, sino que meramente cubren sus costos o llegan inclusive a ser subsidiados por la misma empresa. Sea cual fuere la naturaleza del declive en ventas hay dos opciones, renovar el producto o retirarlo totalmente de la oferta del portafolio y del mercado en s. Esta decisin debe tomarla la empresa en funcin de las necesidades del pblico, y de identificar si no es rescatable, ya que est en declive, lo que quiere decir que viene de grandes ventas.   
