              h4 Relacin de la matriz BCG y el ciclo de vida del producto
      .row.mt-5
        .col-lg-6
          .row.colort4.p-3.marq
            p En la figura 5, se puede correlacionar la relacin existente entre el ciclo de vida del producto y la matriz BCG identificando cmo el crecimiento en ventas se relaciona con la participacin de mercado y el tiempo con la tasa de crecimiento en ventas, lo que sirve para alinear las decisiones estratgicas en la empresa segn el tipo de mercado, su clasificacin y su ciclo de vida de producto.
          .row
          p.mt-5 Un producto con buena participacin en el mercado y un crecimiento en ventas de manera sostenida puede ser la situacin previa a un declive por lo cual la empresa debe hacer este anlisis y prepararse para hacer un reacondicionamiento o mejoramiento a su producto en pro de alargar su etapa de madurez, es decir, tomar las decisiones de defender su posicin relativa de mercado y seguir promoviendo el bien producto o servicio como un bien de calidad de vida para sus consumidores y como una alternativa de consumo para el mercado en general, esto es, que la empresa procurar, dependiendo de la fase en que se encuentre su producto, agilizar su efecto como en el caso del crecimiento, ampliar la fase de maduracin y cuando se empiezan a evidenciar prdidas de atractivo en el mercado y disminucin de ventas, reforzar su producto a efectos de que no caiga en declive. 
        .col-lg-6(data-aos="fade-right")
          img(src='@/assets/curso/tema3/img10.png' alt='AvatarTop')
    p.mt-4 Para la etapa de introduccin los esfuerzos deben ser dinmicos giles, sabiendo canalizar las inversiones para el posicionamiento y conocimiento por ende del producto ante los consumidores potenciales as ir resolviendo el interrogante que representa estos productos para la matriz BCG.
