    p.mt-4 Los procesos de gestin como los procesos de diagnstico empresarial, requieren de informacin y su anlisis permanente, de tal manera que evidencien el estado actual, la evolucin de los mismos, o la tendencia y permitan la toma de decisiones. Una de las herramientas que permite este ejercicio con la informacin en los procesos es la estadstica la cual se define como la ciencia que se dedica al ordenamiento y anlisis de conjuntos de datos para obtener descripciones, explicaciones o predicciones sobre aspectos percibidos y analizados, para ello se describe los tipos de estadstica y los elementos que se deben tener en ella: 
